
@import "../static/css/bootstrap.min.css";
html, body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: Helvetica Neue,Helvetica,Arial,PingFang SC,Heiti SC,Hiragino Sans GB,Microsoft YaHei,\\5FAE\8F6F\96C5\9ED1,sans-serif;
    -webkit-font-smoothing: antialiased;
    color: rgba(0,0,0,.87);
}
#app {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #CE3823;
    color: #fff;
}
.option {
    /*position: fixed;
    top: 20px;
    left: 30px;
    text-align: left;
    background: rgba(206, 56, 35, .1);*/
}
.option-item {
     height: 20px;
     line-height: 20px;
}
.option-item input {
     height: 18px;
     color: #ccc;
}
.option-item input[type=radio] {
     height:13px;
     vertical-align:text-top;
     margin-top:0;
}
.button-prize {
     position: absolute;
     left: 50%;
     margin-left: -30px;
     bottom: 10px;
     color: #333;
     border-radius: 4px;
     border: none;
     background-color: #fff;
}



